.Toolbar {
    height: 56px;
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    /* background-color:#EED8AE; */
    background-color: rgba(19, 33, 77, 0.9);
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 20px;
    box-sizing: border-box;
    z-index: 90;
    margin: 5px 0;
    border:0.15em;
    border-color: white;
    /* box-sizing: border-box; */
}

.Toolbar nav {
    height: 100%;
}

.Logo {
    height: 80%;
}